@tailwind base;
@tailwind components;
@tailwind utilities;

/* @import url("https://fonts.googleapis.com/css2?family=Actor&display=swap"); */
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');
/* @import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Oswald:wght@200..700&display=swap'); */

body {
  background: url("/public/background.svg") no-repeat center top/cover;
}

.btn {
  @apply py-1.5 px-3 sm:px-5 inline-flex items-center font-medium text-sm sm:text-lg rounded-xl border shadow-sm transition-all disabled:opacity-50 disabled:pointer-events-none focus:outline-none focus:bg-gray-100;
}

.btn.light-btn {
  @apply uppercase bg-light-blue text-dark-blue hover:bg-dark-blue hover:text-light-blue;
}

.btn.dark-btn {
  @apply uppercase bg-dark-blue text-light-blue hover:bg-light-blue hover:text-dark-blue;
}

.btn.navy-btn {
  @apply uppercase bg-light-blue text-dark-blue border border-dark-blue hover:bg-dark-blue hover:text-light-blue;
}

.btn.badge-btn {
  @apply bg-light-blue text-light-black;
}

.custom-scrollbar {
  overflow-y: auto;
}

.custom-scrollbar::-webkit-scrollbar {
  width: 8px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: #2131e5;
  border-radius: 10px;
}
#hs-application-sidebar-backdrop {z-index: 40;}
.content-area.about,
.content-area.user-loggedin.instant-links,
.content-area.user-loggedin.import,
.content-area.user-loggedin.bookmarks {
  @apply md:h-[calc(100vh-128px)] xl:h-[calc(100vh-68px)] md:max-h-[1024px]
}
.content-area.user-loggedin.result {
  @apply lg:h-[calc(100vh-128px)] xl:h-[calc(100vh-68px)] lg:max-h-[1024px]
}

.bookmark-sidebar-wrapper {
  @apply w-[340px] xl:w-[375px] p-5 xl:p-0 bg-white xl:bg-transparent;
}

.bookmark-content-wrapper {
  @apply w-full xl:w-[calc(100%-332px)] xl:w-[calc(100%-407px)];
}

.bookmark-sidebar li.active .minus {
  @apply opacity-100;
}

.bookmark-sidebar li.active .plus {
  @apply opacity-0;
}
.loader {
  width: 20px;
  height: 20px;
  border: 3px solid #2131e5;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

.text-truncate {
  display: -webkit-box;
  -webkit-line-clamp: 2; /* Number of lines to display */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
svg.selected-cat path {
  stroke: white;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.import-bookmarks-list h1 {display: none;}
.import-bookmarks-list h3 {@apply font-bold mt-4;}
.import-bookmarks-list dl:first-child {@apply -mt-4;}
.import-bookmarks-list a {@apply text-sm mb-2 inline-block hover:text-dark-blue;}
/* border-gray-200 bg-white text-gray-800 hover:bg-gray-50 */
.google-search {
  background: url("/public/branding.png");
  background-position: left center;
  background-repeat: no-repeat;
  background-color: #ffffff;
}
.password-mask {
  -webkit-text-security: disc;
}
.google-search-wrap > div {
  width: 280px;
}
.google-search-wrap > div > form  {margin-bottom: 0!important;}
.gsc-input-box {padding: 0!important; border-radius: 0!important; height: 34px;}
.gsc-search-button.gsc-search-button-v2 {
  border-radius: 0;
  height: 34px;
  display: flex;
  align-items: center;
  gap:10px;
  border: 2px solid #3079ed;
  background-color: #4d90fe;
  padding: 7px 27px!important;
}
.google-search-wrapper > div {
  width: 70%;
}
table.gsc-search-box td {
  padding: 0 3px!important;
}
.gsst_b {
  height: 34px;
  line-height: 40px;
}